.rafflepress {
  @import "bootstrap-less/bootstrap.less";
}

[v-cloak] {
  display: none !important;
}

body,
.subsubsub,
.tablenav .tablenav-pages {
  color: #222;
}

.subsubsub .current {
  color: #222 !important;
}

.search-box,
.subsubsub {
  margin-bottom: 0px !important;
}

body.rafflepress-body {
  background: #fff;
}

body.rafflepress-body #wpwrap {
  background: #f1f1f1;
}

.rafflepress-builder {
  display: none;
}

.rafflepress-action-txt {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 340px;
  display: inline-block;
}

#rafflepress-giveaway-name {
  width: 100%;
  background: #fbfcfb;
  height: 75px;
  border-bottom: 1px solid #f0f0f0;
  margin-top: 67px;
  display: table;
  padding: 0 23px;
  input {
    height: 40px;
    border-radius: 2px;
    width: 315px;
  }
  label {
    margin-bottom: 0px;
    font-size: 16px;
    margin-right: 15px;
  }
  .form-group {
    display: table-cell;
    vertical-align: middle;
  }
}
.template-chooser {
  h2 {
    margin-top: 0;
    margin-bottom: 15px;
    font-size: 24px;
    color: #2d2d2d;
    font-weight: 400;
  }
  p {
    color: #666;
    font-size: 14px;
    margin-bottom: 30px;
  }
  .highlight-template {
    color: #f1470d !important;
    text-decoration: underline;
    :hover {
      text-decoration: none;
    }
  }
}

.rafflepress-builder #adminmenu,
.rafflepress-builder #wpadminbar {
  display: none !important;
}

#rafflepress-steps-nav {
  border-left: 1px solid #f9f9f9;
  a {
    font-size: 14px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.rafflepress-builder-step .container-fluid {
  padding-left: 23px !important;
  padding-right: 23px !important;
}

.rafflepress-builder-step h1 {
  margin: 20px 0 10px 0 !important;
}

.rafflepress table {
  background-color: #f8f8f8;
}

.rafflepress .navbar-default {
  background-color: #fafbfc;
  border-color: #e8e8e8;
  overflow: hidden;
}

.rafflepress-builder .navbar-default {
  background-color: #fff;
}

.rafflepress .panel-default > .panel-heading {
  color: #222;
  background-color: #f8f8f8;
  border-color: #ddd;
  font-weight: bold;
  position: relative;
}

.widefat thead th,
.widefat thead td {
  border-bottom: 1px solid #ddd;
}

.widget-top,
.menu-item-handle,
.widget-inside,
#menu-settings-column .accordion-container,
#menu-management .menu-edit,
.manage-menus,
table.widefat,
.stuffbox,
p.popular-tags,
.widgets-holder-wrap,
.wp-editor-container,
.popular-tags,
.feature-filter,
.imgedit-group,
.comment-ays {
  border: 1px solid transparent;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  margin-top: 20px;
}

.rafflepress-builder {
  .form-control,
  input[type="text"],
  input[type="password"],
  input[type="checkbox"],
  input[type="color"],
  input[type="date"],
  input[type="datetime"],
  input[type="datetime-local"],
  input[type="email"],
  input[type="month"],
  input[type="number"],
  input[type="search"],
  input[type="radio"],
  input[type="tel"],
  input[type="text"],
  input[type="time"],
  input[type="url"],
  input[type="week"],
  select,
  textarea {
    background-color: #fff;
    border-color: #efefef;
    box-shadow: none;
  }
}

.rafflepress .form-control[disabled],
.rafflepress .form-control[readonly],
fieldset[disabled] .rafflepress .form-control {
  background-color: #fbfcfb;
  opacity: 1;
}

.rafflepress-builder .input-group-addon {
  background-color: #fff;
  border-color: #efefef;
}

.column.entries,
.column.contestants,
.column.active {
  width: 120px;
}

.column.type {
  width: 160px;
}

.column.status {
  width: 210px;
  white-space: nowrap;
}

.column.starts {
  width: 130px;
  white-space: nowrap;
}
.column.ends {
  width: 130px;
  white-space: nowrap;
}

.vue-js-switch .v-switch-core {
  background: #d6d6d6 !important;
}

.vue-js-switch.toggled .v-switch-core {
  background: #4caf50 !important;
}

