//     color: #222;
//     border-color: #ddd;
//     background: #f8f8f8;
//     box-shadow:none;
// }

.rafflepress .navbar-default .navbar-nav > li > a,
.rafflepress .input-group-addon {
  color: #b3b3b3;
}

.rafflepress-builder #rafflepress-sidebar-wrapper .input-group-addon {
  color: #c7c7c7;
  font-size: 13px;
}

.rafflepress .navbar-nav > li > a {
  padding-top: 19px !important;
  padding-bottom: 23px !important;
}

.rafflepress-builder .navbar-nav > li > a {
  padding-top: 23px !important;
  padding-bottom: 23px !important;
}

#rafflepress-steps-nav a span {
  padding: 0 10px 24px;
}

.rafflepress .navbar-default .navbar-nav > .active > a,
.rafflepress .navbar-default .navbar-nav > .active > a:focus {
  color: #222;
  background-color: #fff !important;
  span {
    box-shadow: inset 0 -3px 0px 0px #f1470d !important;
    padding: 0 10px 24px;
  }
}

.rafflepress .navbar-default .navbar-nav > li > a:hover {
  color: #222;
  background-color: #fff !important;
}

.striped > tbody > :nth-child(even),
ul.striped > :nth-child(even),
.alternate {
  background-color: #fff;
}

