#rafflepress-logo {
  width: 220px;
  margin-top: 15px;
}

#rafflepress-builder-logo {
  width: 45px;
  display: inline-block;
  margin: -2px 8px 0 10px;
}

#rafflepress-dashboard {
  padding-right: 20px;
}

.rafflepress-app {
  #poststuff {
    padding-top: 30px;
  }
  .widefat {
    margin-top: 10px !important;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04) !important;
  }
}

#rafflepress-dashboard .column-active {
  width: 90px;
}

#rafflepress-dashboard .column-contestants {
  width: 120px;
}

#rafflepress-dashboard .column-entries {
  width: 120px;
}

#rafflepress-entries .column-entries {
  width: 120px;
}

#rafflepress-entries .fa-trophy {
  padding-right: 5px;
}

#rafflepress-entries #poststuff {
  padding-top: 10px;
}

#giveaway-details-sub {
  color: #999;
  display: block;
  font-size: 10px;
  text-transform: uppercase;
  margin: 0 0 8px 0;
}

#giveaway-details-title {
  float: left;
  font-size: 18px;
  font-weight: 400;
  margin: 0;
  padding-right: 8px;
}

#giveaway-selector {
  display: inline-block;
  position: relative;
}

#rafflepress-entries {
  margin-top: 20px;
}

#rafflepress-entries .toggle.active {
  background-color: #e3e3e3;
  outline: none;
}

#rafflepress-entries .toggle {
  border-radius: 50%;
  color: #444;
  font-size: 16px;
  box-shadow: none;
}

#rafflepress-entries .toggle:before {
  vertical-align: middle;
}

#giveaway-details-actions {
  margin-top: 26px;
  text-align: right;
}

.giveaway-details-actions {
  margin-right: 12px;
}

.giveaway-details-actions:last-child {
  margin-right: 0px;
}

.giveaway-selector {
  display: inline-block;
  position: relative;
}

.giveaway-selector ul {
  max-height: 196px;
  overflow-x: hidden;
  padding: 0;
  margin: 0;
}

#raffleprsss-giveaway-list-loading {
  text-align: center;
  display: block;
}

.giveaway-selector ul li {
  list-style: none;
  margin: 0;
  font-size: 13px;
}

.giveaway-selector ul li a {
  display: block;
  text-decoration: none;
  padding: 5px 15px;
  color: #444;
}

.giveaway-selector ul li a:hover {
  background-color: #f7f7f7;
  color: #444;
}

#winner-selector {
  display: inline-block;
  position: relative;
}

#rafflepress-winner-options {
  background: #fff;
  border-radius: 3px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 69px;
  left: -15px;
  width: 300px;
  z-index: 9992;
  font-size: 13px;
  font-weight: normal;
  padding: 20px;
  input[type="radio"] {
    margin-top: 3px;
  }
}

#rafflepress-winner-options:before {
  content: " ";
  position: absolute;
  top: -10px;
  left: 145px;
  width: 0;
  height: 0;
  border-style: solid;

  border-width: 0 8px 10px 8px;
  border-color: transparent transparent #fff transparent;
}

#giveaway-list {
  background: #fff;
  border-radius: 3px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 27px;
  left: -108px;
  width: 230px;
  padding: 10px 0;
  z-index: 9991;
}

#giveaway-list:before {
  content: " ";
  position: absolute;
  top: -10px;
  left: 110px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 8px 10px 8px;
  border-color: transparent transparent #fff transparent;
}

#rafflepress-header {
  position: relative;
  margin-left: -20px;
}

.rafflepress-header-margin {
  margin-top: 20px !important;
}

.rafflepress-entry-options {
  margin-bottom: 5px;
}

.rafflepress-entry-options .btn-my-entry-option {
  padding: 2px 5px;
}

.rafflepress .navbar-right {
  margin-right: 0;
  border-color: transparent;
  border-bottom-color: #e7e7e7;
}

.rafflepress-breadcrumbs {
  display: inline-block;
  margin-top: 10px;
  margin-bottom: 20px;
}

#rafflepress-page-title {
  background-color: #fff;
  margin: 0 0 0px -20px;
  padding: 15px 20px;
  font-size: 23px;
  font-weight: 400;
  line-height: 29px;
  border-bottom: 1px solid #e7e8e7;
  .add-new-h2 {
    margin-left: 28px;
  }
}

.rafflepress-btn-orange {
  background-color: #f1460d !important;
  border-color: #f0460d !important;
  color: #fff !important;
}

.rafflepress-btn-orange:hover {
  background-color: #c1390a !important;
  border-color: #c1390a !important;
  color: #fff !important;
}

.rafflepress-breadcrumbs small {
  font-size: 14px;
}

.rafflepress .container-fluid {
  padding: 0;
}

.d-ib {
  display: inline-block;
}

.rafflepress .tablenav {
  margin: 0px 0 4px;
}

.rafflepress h1 {
  margin: 0 0 20px 0;
  padding: 0;
}

.rafflepress-default-cusor {
  cursor: default;
}

.rafflepress td,
.rafflepress th {
  padding: 10px;
}

.rafflepress :focus {
  outline: none !important;
  box-shadow: none !important;
}

/* Builder */
#rafflepress-search-close {
  margin-left: -24px;
  margin-right: 11px;
  color: #aaa;
  z-index: 999;
  position: relative;
  cursor: pointer;
}
#rafflepress-customizer {
  background: #f8f8f8;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 99999;
  height: 100%;
}

#rafflepress-customizer-wrapper {
  height: 100%;
  padding-top: 67px;
}

.rafflepress-builder .mx-input,
#rafflepress-sidebar-wrapper .form-control {
  font-size: 13px;
  height: 40px;
}

.rafflepress-builder .btn i {
  color: rgba(0, 0, 0, 0.34);
}

.rafflepress-builder .rafflepress-sidebar-form-body .btn i {
  margin-right: 4px;
}
.rafflepress-builder .rafflepress-settings-page-wrapper .btn i {
  margin-right: 4px;
}

.rules-modal {
  .rafflepress-modal-container {
    border-radius: 6px;
    max-width: 620px;
    position: relative;
  }
  hr {
    margin-top: 15px;
    margin-bottom: 25px;
  }
  .rafflepress-modal-body {
    margin-top: 0;
  }
  i {
    color: #ccc;
    cursor: pointer;
    position: absolute;
    top: 20px;
    font-size: 16px;
    right: 20px;
  }
}

.rafflepress-modal-body {
  h3 {
    font-size: 20px !important;
  }
  label {
    font-size: 14px !important;
  }
}

#rafflepress-sidebar-nav-wrapper {
  background-color: #2d2d2d;
  color: #a3a3a3;
  z-index: 200;
  left: 0px;
  position: absolute;
  bottom: 0;
  top: 67px;
  right: auto;
  width: 95px;
  overflow-y: auto;
  overflow-x: hidden;
  .active {
    background-color: #000;
    color: #fff;
  }
}

#rafflepress-sidebar-wrapper {
  background-color: #fcfcfc;
  z-index: 200;
  left: 95px;
  position: absolute;
  bottom: 0;
  top: 67px;
  right: auto;
  width: 315px;
  overflow-y: auto;
  border-right: 1px solid #f5f4f5;
  //box-shadow: 5px 0px 8px rgba(0,0,0,0.1);
}

#rafflepress-preview-wrapper {
  padding-left: 410px;
  height: 100%;
  overflow-y: scroll;
  background: #f8f8f8;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
  background-attachment: fixed;
}

.rafflepress-sidebar-form-body {
  padding: 20px;
  border-top: 2px solid #f4f4f4;
  border-bottom: 1px solid #f0f0f0;
  background: #fff;
  text-align: left;
  textarea {
    height: 150px !important;
  }
  .col-md-6:first-child {
    padding-right: 7px;
  }
  .col-md-6:last-child {
    padding-left: 7px;
  }
  .help_tip i {
    vertical-align: text-top;
    color: #c7c7c7;
    margin-left: 3px;
  }
}

.rafflepress_collaspe {
  border-bottom: 2px solid #f4f4f4;
}

.rafflepress-sidebar-form h3 {
  font-size: 14px;
  color: #2d2d2d;
  margin: 0;
  padding: 20px 20px;
  background: #fbfcfb;
  display: block;
  cursor: pointer;
}

.rafflepress-sidebar-form h3::after {
  text-align: right;
  float: right;
  content: "";
  background-image: url(../img/down-arrow.png);
  background-repeat: no-repeat;
  background-size: auto;
  background-position: 0;
  width: 13px;
  height: 13px;
}

.rafflepress-sidebar-form h3.rafflepress_collaspe::after {
  text-align: right;
  float: right;
  content: "";
  background-image: url(../img/right-arrow.png);
  background-repeat: no-repeat;
  background-size: auto;
  background-position: 0;
  width: 13px;
  height: 13px;
}

#rafflepress-dragbar {
  background-color: #ccc;
  height: 100%;
  width: 3px;
  cursor: col-resize;
  position: absolute;
  right: 0;
